<template>
  <div id="rooms">
    <head-top :title="getRooomName" goBack="true"></head-top>
    <div class="content" >
      <div class="full" v-if="false">
        <img src="../../assets/device/nothing@3x.png" alt="">
        <h3>No devices</h3>
        <p>Please add a roomate smart device by clicking below</p>
      </div>
      <div class="list" v-if="true">
        <ul>
          <li class="list_li" v-for="item in deviceData" :key="item.id">
            <div class="left" @click="go(item)">
              <img :src="item.icon" alt="">
              <span>{{item.device}}</span>
            </div>
            <div class="right" @click="deviceTask(item)">
              <span>{{item.status}}</span>
              <img src="../../assets/device/on@3x.png" alt="" v-if="item.switch">
              <img src="../../assets/device/off@3x.png" alt="" v-if="!item.switch">
            </div>
          </li>
        </ul>
        <span class="options">Press on your device to view</span>
      </div>
      <div class="button" @click="next">
        Add Devices
      </div>
    </div>
    <transition name="slide-left" mode="in-out">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import SideBar from '../../components/common/sideBar.vue'
import headTop from '../../components/common/head'
import messageBox from '../../components/common/messageBox'
import {mapGetters, mapActions} from 'vuex'

const heartIMG = require('../../assets/device/heater@3x.png')
const humideyIMG = require('../../assets/device/humidier@3x.png')
const lightIMG = require('../../assets/device/light@3x.png')
export default {
  components: {
    'side-bar': SideBar,
    'head-top': headTop,
    messageBox
  },
  data () {
    return {
      deviceData: [] // device list
    }
  },
  computed: {
    ...mapGetters(['roomName']),
    getRooomName () {
      return this.roomName
    }
  },
  mounted () {
    this.deviceData = [
      {
        icon: heartIMG,
        device: 'Heater',
        status: '25%',
        switch: true
      },
      {
        icon: humideyIMG,
        device: 'Humidier',
        status: '50%',
        switch: true
      },
      {
        icon: lightIMG,
        device: 'Light 1',
        status: 'ON',
        switch: true
      },
      {
        icon: lightIMG,
        device: 'Light 2',
        status: 'OFF',
        switch: true
      }
    ]
  },
  methods: {
    ...mapActions(['addevicePath']),
    go (item) {
      // --->crotrol
      this.$router.push({'path': '/temperatureRoom', query: {'device': item.device}})
    },
    next () {
      this.addevicePath('/Me/rooms')
      this.$router.push({path: '/Me/addevice'})
    },
    deviceTask (item) {
      item.switch = !item.switch
    }
  }
}
</script>

<style scoped lang="less">
#rooms{
  .fs;
  background: #ffffff;
  z-index: 1;
  .wh(100%, 100%);
  background: url(../../assets/global/bg-@3x.png);
  background-size: cover;
  .content, .content_2{
    .wh(100%, 100%);
    display: flex;
    flex-direction: column;
    .full{
      align-self: center;
      display: flex;
      flex-direction: column;
      // height: 63%;
      img{
        .wh(234px, 270px);
        margin-top: 95px;
        align-self: center;
      }
      h3{
        font-size: 34px;
        margin-top: 60px;
        font-weight: 400;
      }
      p{
        width: 100%;
        font-size: 28px;
        padding: 69px 82px 0 52px;
        align-self: center;
        color: #898989;
        line-height: 44px;
        text-align: left;
      }
    }
    .list{
      .wh(100%, 100%);
      display: flex;
      flex-direction: column;
      ul{
        display: flex;
        flex-direction: column;
        margin-top: 30px;
        background: white;
        overflow: hidden;
        li{
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          .wh(100%, 93px);
          border-top: 1px solid #dadada;
          padding:0 30px;
          line-height: 93px;
          position: relative;
          .left{
            width: 80%;
            img{
              .wh(23px,38px);
              margin-right: 30px;
              align-self: center;
            }
            display: flex;
            span{
              font-size: 30px;
              color: #575757;
            }
          }
          .right{
            display: flex;
            width: 20%;
            justify-content: flex-end;
            span{
              font-size: 23px;
              color: #c4c6c5;
            }
            img{
              .wh(60px,30px);
              align-self: center;
              margin-left: 30px;
            }
          }
        }
        li:last-child {
          border-bottom: 1px solid #dadada;
        }
      }
      .options{
        font-size: 26px;
        color: #000;
        margin-top:35px;
      }
    }
    .button{
      .wh-l(520px;105px);
      color: white;
      font-size: 45px;
      background: #13af68;
      border-radius: 10px;
      position: absolute;
      bottom: 18%;
      left: 50%;
      margin-left: -34.6%;
    }
  }
  .common_alert{
    .alert_icon{
      font-size: 33px;
      font-weight: 600;
      color: #585858;
      margin-top: 72px;
    }
    .alert_describe{
      font-size: 28px;
      margin-top: 57px;
      color: #666666;
    }
    .alert_operation{
     font-size: 32px;
     color: white;
     display: flex;
     position: absolute;
     bottom: 0;
     width: 100%;
    }
    .determine_one{
      .wh-l(50%,110px);
    }
    .rename{
      margin: 0 86px;
     .wh-l(438px, 80px);
      display:flex;
      border-bottom: 1px solid #cdcdcd;
      img{
        .wh(24px, 24px);
        display: block;
        align-self: center;
      }
      input{
        .wh(100%,100%);
        font-weight: 900;
        color: #585858;
      }
    }
    .describe{
      .wh(438px, auto);
      margin:  0 auto;
      color: #cccccc;
      font-size: 22px;
      line-height: 30px;
      margin-top: 30px;
    }
  }
  .messageToast{
    display: flex;
    z-index: 10;
    .wh(300px, 260px);
    position: absolute;
    top:50%;
    left: 50%;
    margin: -240px 0 0 -130px;
    background: url(../../assets/device/smallspring@3x.png);
    // box-shadow: 20px 20px 10px 3px #7e7b7b;
    // background: white;
    background-size: cover;
    // border-radius: 5%;
    flex-direction: column;
    div{
    display: flex;
    flex-direction: column;
    .wh(100%,100%);
    }
    img{
      .wh(72px, 72px);
      margin-top: 55px;
      align-self: center;
    }
    .Rotation{
      animation: Rotation 1.5s infinite linear;
    }
    @keyframes Rotation {
      0%{
        transform: rotate(0deg)
      }
      // 20%{
      //   transform: rotate(72deg)
      // }
      // 40%{
      //   transform: rotate(144deg)
      // }
      // 60%{
      //   transform: rotate(216deg)
      // }
      // 80%{
      //   transform: rotate(278deg)
      // }
      100%{
        transform: rotate(360deg)
      }
    }
    span{
      // .wh(160px, auto);
      padding: 0 38px;
      font-size: 25px;
      margin:  0 auto;
      margin-top: 30px;
    }
  }
  .content_2{
    .full{
      h3{
        width: 79%;
        font-weight: 400;
        color: #5d5d5d;
        align-self: center;
        line-height: 67px;
      }
      p{
        width: 54%;
        margin-top: 15px;
      }
    }
    .list ul li{
      img{
        .wh(25px; 40px);
      }
    }
  }
}
</style>



